.node {
  width: 25px;
  height: 25px;
  border-right: 1px solid rgb(175, 216, 248);
  border-bottom: 1px solid rgb(175, 216, 248);
  display: table-cell;
}

.node-finish {
  background-color: #FF6766;
}

.node-start {
  background-color: rgb(116, 216, 163);
}

.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: .6s;
          animation-duration: .6s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: #d6eaff;
    /* background-color: #fde0e0; // red */
    border-radius: 100%;
  }

  50% {
    background-color:#add6ff;
    /* background-color: #fdaaaa; */
  }

  75% {
    transform: scale(1.1);
    background-color:#add6ff;
    /* background-color:	#fdaaaa; */
  }

  100% {
    transform: scale(1);
    background-color: #84c1ff;
    /* background-color:	#f97c7c; */
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: #d6eaff;
    /* background-color: #fde0e0; // red */
    border-radius: 100%;
  }

  50% {
    background-color:#add6ff;
    /* background-color: #fdaaaa; */
  }

  75% {
    transform: scale(1.1);
    background-color:#add6ff;
    /* background-color:	#fdaaaa; */
  }

  100% {
    transform: scale(1);
    background-color: #84c1ff;
    /* background-color:	#f97c7c; */
  }
}

.node-wall {
  -webkit-animation-name: wallAnimation;
          animation-name: wallAnimation;
  -webkit-animation-duration: .3s;
          animation-duration: .3s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  
}

@-webkit-keyframes wallAnimation { 
  0% {
    transform: scale(0.5);
    background-color: rgb(42, 54, 59);
  }

  50% {
    transform: scale(1.1);
    background-color: rgb(42, 54, 59);
  }

  100% {
    transform: scale(1);
    background-color: rgb(42, 54, 59);
  }
}

@keyframes wallAnimation { 
  0% {
    transform: scale(0.5);
    background-color: rgb(42, 54, 59);
  }

  50% {
    transform: scale(1.1);
    background-color: rgb(42, 54, 59);
  }

  100% {
    transform: scale(1);
    background-color: rgb(42, 54, 59);
  }
}

.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: 	#e1ffe9;
  }

  50% {
